import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, ViewController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
declare var OpenPay

@IonicPage()
@Component({
  selector: 'page-tarjeta-add',
  templateUrl: 'tarjeta-add.html',
})
export class TarjetaAddPage {

  private sess_id: string = OpenPay.deviceData.setup()
  public addData: any = {}

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    private load: LoadingController,
    private viewCtrl: ViewController
  ) {
  }

  ionViewDidLoad() {
    
  }

  generateToken(data: any): Promise<any> {
    return new Promise((resolve, reject) => {
      OpenPay.token.create(data, token => {
        resolve(token)
      }, err => {
        console.log(err)
        this.toast.create({message: 'No se pudo generar el Token...', duration: 2000}).present()
        reject(err)
      })
    })
  }

  async addCard(data) {

    if (!data) return
    const l = this.load.create({content: 'Agregando Tarjeta...'})
    l.present()

    if (!this.back.validate(data, ['holder_name', 'card_number', 'cvv2', 'expiration_month', 'expiration_year'], this.toast)) return l.dismiss()

    if (!OpenPay.card.validateCardNumber(data.card_number)) {
      l.dismiss()
      return this.toast.create({message: 'El número de tarjeta es inválido...', duration: 2000}).present()
    }
    if (!OpenPay.card.validateCVC(data.cvv2)) {
      l.dismiss()
      return this.toast.create({message: 'El código de seguridad es inválido...', duration: 2000}).present()
    }

    try {
      const token = await this.generateToken(data)
      console.log(token);
      
      this.back.addTarjeta({
        elputoid: this.back.uid,
        token: token.data.id,
        sess_id: this.sess_id
      }).subscribe(newCard => {
        console.log(newCard)
        if (newCard.e) {
          this.toast.create({message: 'Tarjeta inválida. Código ' + newCard.e.error_code, duration: 2000}).present() 
          l.dismiss()
        } else {
          this.viewCtrl.dismiss(newCard)
          l.dismiss()
        }
      }, err => {
        console.log(err)
        this.toast.create({message: 'Tarjeta inválida. Código 3001', duration: 2000}).present()
        l.dismiss()
      })
    } catch (error) {
      console.log(error)
      l.dismiss()
    }

  }

}



